import { _decorator, Component, Node, instantiate, Vec3, Vec2 } from "cc";
import { EasyLevels } from "../Levels/EasyLevels";
import { InGameAssets } from "../InGameAssets";
import { LevelData } from "../Levels/LevelData";
const { ccclass, property } = _decorator;

@ccclass("GridGenerator")
export class GridGenerator extends Component {

  start() {
    this.generate(EasyLevels.levels[3]);
  }

  update(deltaTime: number) {}

  private setNodeActive(settingNode: Node, isActive: boolean) {
    settingNode.active = isActive;
  }

  generate(level: LevelData) {
    const rows = level.rows;
    const cols = level.cols;
    const gridData = level.grid;
    const tileSize = 100; // burayı değiştir

    for (let row = rows-1; row >0; row--) {
      for (let col = cols-1; col > 0; col--) {
        const currentClass = gridData[col][row];
        const assetType = InGameAssets.AssetClass.get(currentClass);
        
        if (assetType && assetType.length > 0) {
          const randomIndex = Math.floor(Math.random() * assetType.length );
          const randomAsset = assetType[0];

          if (randomAsset) {
            const newNode = instantiate(randomAsset);
          
            this.node.addChild(newNode);
            const position = this.getTilePosition(row, col, tileSize);
            newNode.setPosition(position);
            newNode.active = true;
          }
        }
      }

      //to center grid
    //   const newPosition = new Vec3(-1 * (tileSize*2),100,0)
    //   this.node.setPosition(newPosition )
    }
  }

  // Griddeki her bir tile'ın pozisyonunu hesaplar
  private getTilePosition(row: number, col: number, tileSize: number): Vec3 {
    const x = col * tileSize
    const y = row * tileSize
    console.log(x,y);
    
    return new Vec3(x, y, 0);
  }
}
